@import './var.scss';
@import './theme.scss';
@import './mixins';

.farm-modal-input.ant-input-affix-wrapper {
  padding: 10px;
  background: $color-large-range-card-bg;
  height: 40px;
  border: none;
  .ant-input {
    background: inherit;
    color: $color-text-default;
    font-size: 16px;
    &::placeholder {
      color: $color-text-default-5;
    }
  }
  .suffix-text {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
    margin-right: 8px;
  }
  .max-btn {
    background: $color-max-btn-bg;
    border-radius: 15px;
    border: 1px solid $color-max-button-border;
    padding: 4px 14px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    color: $color-max-button-text;
  }
  .purple {
    background: $purple;
    color: $white;
  }
}
.farm-modal-input.ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
  border-color: $blue;
}

html body button.common-btn {
  width: 100%;
  line-height: 40px;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: -0.3px;
  color: $white;
  text-align: center;
  background: $blue;
  border-radius: 10px;
  border: none;
  box-shadow: none;
  cursor: pointer;

  &:disabled {
    opacity: 0.3;
    color: $white;
    cursor: not-allowed !important;
  }

  &.mt-ers {
    margin-top: 60px;
  }

  &.border {
    border: 1px solid $color-a-default !important;
    color: $color-a-default !important;
    background: $white !important;

    &:disabled {
      color: rgba($blue, 0.3) !important;
      cursor: not-allowed !important;
      border-color: rgba($blue, 0.3) !important;
    }
  }
}

.common-error {
  margin-top: 10px;
  color: $warning;
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
}

.jcfe {
