.no-data {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 310px;
  text-align: center;
  // background: rgba(56, 51, 72, 0.9);
  background: $color-card-bg-default;
  border-radius: 15px;
  margin-bottom: 100px;

  .no-data-spin {
    width: 50px;
    height: 50px;
    margin-bottom: 10px;
    animation: stakeRotate 1s infinite linear;
  }

  .no-data-img {
    width: 106px;
    height: 80px;
    margin-bottom: 10px;
  }
  > div {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: $color-text-default;
  }
}

.lock-price {
  margin-top: 10px;
  > span {
    font-family: 'Helvetica Neue', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #999999;
  }
  > a {
    text-align: right;
    font-size: 14px;
    line-height: 14px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: $color-a;
    cursor: pointer;
    &:link,
    &:visited {
      color: $color-a;
    }
    &:hover {
      color: $color-a-hover;
    }
  }
  .warning {
    color: $warning;
  }
  .error {
    color: #ed635a;
  }
}

.new-stake-sub-nav {
  display: block;
  width: 100%;
  overflow-y: inherit;
  overflow-x: auto;
  white-space: nowrap;
  -ms-overflow-style: none; /* IE 10+ */
  scrollbar-width: none; /* Firefox */
  &::-webkit-scrollbar {
    display: none;
  }
  .sub-nav-item {
    margin-right: 30px;
    display: inline-block;
    &:first-child,
    &:last-child {
      font-size: 14px;
    }
    &:first-child {
      &:after {
        display: none;
      }
    }
  }
}

// 公共的css
.ant-modal.farm-modal {
  z-index: 10000;
  transform-origin: center;
  // 标题
  .lock-desc-title {
    font-size: 16px;
    font-weight: 600;
    color: $color-text-default;
    line-height: 22px;
    margin-bottom: 10px;
  }
  // 信息模块
  .lc-info {
    background: $color-info-bg;
    border-radius: 5px;
    border: 1px solid $color-text-default-1;
    padding: 16px;

    > p {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin: 0 0 16px 0;

      &:last-child {
        margin: 0;
      }

      > span {
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: $color-text-default-7;
        word-break: break-all;

        &:first-child {
          white-space: nowrap;
        }

        &:last-child {
          font-size: 14px;
          font-weight: 600;
          color: $color-text-default;
          text-align: right;
        }
      }
    }
    .old-data img {
      width: 11px;
    }
    img {
      width: 14px;
      height: 14px;
      margin: 0 6px;
    }
  }
  .lc-info-important {
    background: $color-info-special-card-bg;
  }
  // 左右布局信息模块
  .lc-info-box {
    background: $color-info-card-bg;
    border-radius: 5px;
    border: 1px solid $color-text-default-1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 0;

    > div {
      flex: 1;
      width: 50%;
      padding: 0 16px;
      text-align: center;
      &:nth-child(1) {
        border-right: solid 1px $color-text-default-1;
      }
      > span:nth-child(1) {
        font-size: 12px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: $color-text-default-7;
        line-height: 17px;
        margin-bottom: 10px;
        display: block;
      }
      > span:nth-child(2) {
        font-size: 16px;
        font-weight: 600;
        color: $color-text-default;
        line-height: 22px;
      }
    }
  }
  // 余额
  .lock-balance {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
    line-height: 20px;
    .op-5 {
      opacity: 0.5;
    }
  }
  // warning
  .warning {
    .warning-title {
      font-size: 14px;
      font-weight: 600;
      color: $color-text-default;
      line-height: 20px;
      > img {
        width: 14px;
        height: 14px;
        margin-left: 4px;
      }
    }

    .warning-content {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default-5;
      .purple-color {
        color: $color-a;
      }
    }
  }
}

// 锁定sun弹窗
.ant-modal.lock-sun-modal {
  .lock-desc-title2 {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-5;
    line-height: 18px;
    margin: 0;
  }
  .lock-sub-title {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
  }

  // 时间模块
  .lc-date {
    display: flex;
    flex-flow: wrap;
    > span {
      flex: 1;
      padding: 7px 0;
      text-align: center;
      border: solid 1px $color-text-default-1;
      margin: 0 8px 0px 0;
      border-radius: 5px;
      font-size: 12px;
      color: $color-a;
      cursor: pointer;

      &:last-child {
        margin-right: 0;
      }

      &.current {
        border: none;
        background: $blue;
        color: $white;
      }
    }
  }

  .error {
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #ed635a;
    height: 17px;
  }
  .ant-tabs-nav {
    &::before {
      border-bottom: none;
    }
    .ant-tabs-tab {
      font-size: 20px;
      font-weight: 600;
      color: $color-text-default-5;
      padding: 0;
      margin-bottom: 4px;
    }
    .ant-tabs-tab-active {
      color: $color-text-default;
      .ant-tabs-tab-btn {
        color: $color-text-default;
      }
    }
    .ant-tabs-ink-bar {
      transform: scaleX(0.3);
      background: $purple-btn;
      height: 3px;
    }
  }
}

// 质押弹窗
.ant-modal.stake-token-modal {
  a {
    font-size: 14px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: $color-a;
    cursor: pointer;
    &:link,
    &:visited {
      color: $color-a;
    }
    &:hover {
      color: $color-a-hover;
    }
  }
  .stake-balance {
    display: flex;
    justify-content: space-between;
    align-items: center;
    > span:nth-child(1) {
      font-size: 16px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      color: $color-text-default;
    }
    > span:nth-child(2) {
      font-size: 14px;
      font-weight: 600;
      color: $color-text-default;
      span {
        opacity: 0.5;
      }
    }
  }
  .line-gray {
    margin: 10px 0 20px;
  }
}

// fixed lock 弹窗
.ant-modal.fixed-lock-modal {
  .lm-radio-btn {
    display: flex;
    border: 1px solid $color-text-default-3;
    border-radius: 2px;
    height: 32px;
    button {
      background: transparent;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      border: none;
      cursor: pointer;
      height: 30px;
      line-height: 14px;
      &:nth-child(3) {
        border-radius: 0px 2px 2px 0px;
      }
    }
    .active {
      color: $white;
      background: $blue;
      border: none;
    }
  }
  .extend-check {
    .extend-check-text {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      line-height: 20px;
    }
    .ant-checkbox-wrapper .ant-checkbox:not(.ant-checkbox-disabled) {
      .ant-checkbox-inner {
        background-color: transparent;
        border-color: $color-text-default-5;
      }
      &:hover {
        .ant-checkbox-inner {
          background-color: transparent;
          border-color: $color-text-default-5;
        }
      }
    }
    .ant-checkbox-wrapper .ant-checkbox.ant-checkbox-checked {
      .ant-checkbox-inner {
        background-color: $blue;
        border: none;
      }
      &::after {
        border: none;
      }
      &:hover {
        .ant-checkbox-inner {
          background-color: $blue;
          border: none;
        }
      }
    }
    .ant-checkbox-wrapper .ant-checkbox.ant-checkbox-disabled {
      .ant-checkbox-inner {
        background: $color-text-default-3;
        border: none;
        &::after {
          border-color: $color-text-default-5;
        }
      }
      &:hover {
        .ant-checkbox-inner {
          background: $color-text-default-3;
          border: none;
          &::after {
            border-color: $color-text-default-5;
          }
        }
      }
    }
  }
  .lm-date {
    display: flex;
    flex-wrap: wrap;
    > span {
      flex: 1;
      width: auto;
      padding: 7px 0;
      text-align: center;
      border-radius: 5px;
      border: 1px solid $color-text-default-1;
      margin: 0 14px 0 0;
      font-size: 14px;
      font-weight: 600;
      color: $color-a;
      cursor: pointer;

      &:last-child {
        margin-right: 0;
      }

      &.current {
        border: none;
        color: $white;
        background-color: $blue;
      }
    }
  }
  .lm-input {
    align-items: center;
    .pretext {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      white-space: nowrap;
    }
    .errorWeek {
      .ant-input {
        color: $warning;
      }
    }

    .ant-input {
      &::placeholder {
        color: $color-text-default;
        opacity: 0.3;
      }
    }
    .ant-input-affix-wrapper {
      height: 34px;
      line-height: 34px;
      border: 1px solid $color-text-default-1;
      background: $color-text-default-1;
    }
    .ant-input {
      text-align: left;
      background: transparent;
      color: $color-text-default;
    }
    .ant-input-suffix {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default-7;
      line-height: 20px;
    }
  }
  .common-error {
    text-align: right;
  }
  .link-text {
    color: $color-a;
    font-size: 14px;
  }
  .big-amount {
    > span:nth-child(1) {
      font-size: 24px;
      font-weight: 600;
      color: $color-text-default;
    }
    > span:nth-child(2) {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default-5;
    }
  }
}

// 倒计时弹窗
.ant-modal.top-modal {
  .ant-statistic-title {
    font-size: 20px;
    font-weight: 600;
    color: $color-text-default;
  }
  .ant-statistic-content {
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    color: $color-text-default;
  }
  .ant-spin {
    display: flex;
    justify-content: center;
    padding: 20px;
    .ant-spin-dot-item {
      background-color: $color-text-default;
    }
  }
}

// tips弹窗
.ant-modal.tips-modal {
  .step {
    border-radius: 15px;
    margin-bottom: 12px;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 180px;

    &:last-child {
      margin-bottom: 0px;
    }

    p {
      width: 52%;
      margin: 0;
    }

    .ant-btn {
      width: fit-content;
      height: 40px;
      border-radius: 5px;
      font-size: 16px;
      line-height: 22px;
      border: none;
      margin-top: 12px;
      color: $white;
      &:hover {
        color: $white;
      }
    }
  }

  .step_1 {
    @include darkThemeOnly {
      color: #ffc860;
    }
    @include lightThemeOnly {
      color: #ff8c00;
    }
    background: url(../images/stake_tips_step_bg1.png) right / cover no-repeat;

    .ant-btn {
      background: linear-gradient(139deg, #ffb500 0%, #ff7a00 100%);
      &:hover {
        background: linear-gradient(139deg, #ffb500 0%, #ff7a00 100%);
      }
    }
  }

  .step_2 {
    @include darkThemeOnly {
      color: #8bd0ff;
    }
    @include lightThemeOnly {
      color: #0098ff;
    }

    background: url(../images/stake_tips_step_bg2.png) right / cover no-repeat;

    .ant-btn {
      background: linear-gradient(139deg, #517eff 0%, #673eff 100%);
      &:hover {
        background: linear-gradient(139deg, #517eff 0%, #673eff 100%);
      }
    }
  }

  .step_3 {
    @include darkThemeOnly {
      color: #ecaaff;
    }
    @include lightThemeOnly {
      color: #b52fff;
    }
    background: url(../images/stake_tips_step_bg3.png) right / cover no-repeat;

    .ant-btn {
      background: linear-gradient(139deg, #b43eff 0%, #8421ff 100%);
      &:hover {
        background: linear-gradient(139deg, #b43eff 0%, #8421ff 100%);
      }
    }
  }
}

// diff弹窗
.ant-modal.diff-modal {
  .common-text {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-7;
    line-height: 20px;
  }
  .modal-text {
    padding: 14px 16px;
    border-radius: 15px;
    .title {
      font-size: 16px;
      font-weight: 600;
      line-height: 24px;
      margin-bottom: 10px;
    }
    .detail {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      line-height: 20px;
    }
  }
  .flexible-text {
    .detail {
      width: 48%;
    }
    @include darkThemeOnly {
      color: #ffc860;
    }
    @include lightThemeOnly {
      color: #ff8c00;
    }
    background: url(../images/new-farm/flexible_bg.png) right / cover no-repeat;
  }
  .lock-text {
    .detail {
      width: 48%;
    }
    @include darkThemeOnly {
      color: #ecaaff;
    }
    @include lightThemeOnly {
      color: #b52fff;
    }
    background: url(../images/new-farm/lock_bg.png) right / cover no-repeat;
  }
  .unlock-text {
    .detail {
      width: 58%;
    }
    @include darkThemeOnly {
      color: #ffc860;
    }
    @include lightThemeOnly {
      color: #ff8c00;
    }
    background: url(../images/new-farm/unlock_bg.png) right / cover no-repeat;
  }
  .claim-text {
    .detail {
      width: 60%;
    }
    @include darkThemeOnly {
      color: #ecaaff;
    }
    @include lightThemeOnly {
      color: #b52fff;
    }
    background: url(../images/new-farm/claim_bg.png) right / cover no-repeat;
  }
