// vest弹窗
.ant-modal.vest-modal {
  .vest-box {
    background: $color-info-card-bg;
    border-radius: 5px;
    border: 1px solid $color-text-default-1;
    padding: 16px 0;
    &-title {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default-7;
      line-height: 22px;
    }
    &-content {
      font-size: 16px;
      font-weight: 600;
      color: $color-text-default;
      line-height: 32px;
    }
    > div {
      display: flex;

      > span {
        flex: 1 1;
        text-align: center;
        border-right: solid 1px $color-text-default-1;
        &:last-child {
          border-right: none;
        }
      }
    }
  }
}

// lock detail 弹窗
.detail-modal {
  .detail-modal-box {
    background: $color-info-card-bg;
    border-radius: 5px;
    &-header {
      display: flex;
      justify-content: space-between;
      background: $color-text-default-06;
      padding: 0 16px;
      height: 40px;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default-7;
      line-height: 40px;
    }
    &-content {
      display: flex;
      justify-content: space-between;
      border-bottom: solid 1px $color-text-default-1;
      margin: 0 16px;
      padding: 10px 0px;
      font-size: 14px;
      font-weight: 600;
      color: $color-text-default;
      &:last-child {
        border-bottom: none;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .wrap,
  .banner.pool-config {
    width: 90% !important;
  }
}

@media screen and (max-width: 1200px) {
  .stake-hide-for-mobile {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .tab-active {
    font-size: 16px;
  }
  .sub-tab {
    font-size: 12px !important;
  }
}

@media screen and (max-width: 500px) {
  .get-lp {
    > button {
      &:first-child {
        // margin-bottom: 10px !important;
      }
      &.add-btn-status2 {
        margin-bottom: 0 !important;
      }
    }
  }
}

@keyframes fly {
  0% {
    top: -11px;
  }
  100% {
    top: -266px;
  }
}

@keyframes hide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes bgMove {
  0% {
    background-position: 1200% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@keyframes opacityAni {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes stakeRotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
